.navbar-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navbar {
  z-index: 2;
  width: 100%;
  height: 60px;
  box-shadow: 0px 1px 10px gray;
  /* background: blue; */
  position: fixed;
  top: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: white;
}

.navbar .navbar-introduce {
  background-color: green;
  width: 40%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-introduce .introduce-text {
  margin-left: 5px;
  height: 100%;
  /* background-color: gray; */
  /* content: "dasdasds"; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.introduce-text .name {
  margin-bottom: -20px;
}

.navbar .logo {
  width: 40px;
  height: 40px;
  border-radius: 20%;
  background-color: blue;
  margin-left: 20px;
  /* z-index: 4; */
  /* position: relative; */
}

.navbar .navbar-introduce .avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.navbar .button-log {
  width: 90px;
  height: 90%;
  margin-right: 5px;
  background: white;
  /* box-shadow: 1px 0px 3px gray; */
  font-size: medium;
  border: none;
  margin-right: 20px;
  border: 1px solid black;
}
.navbar .button-log:hover {
  background: rgb(240, 237, 237);
  color: white;
}
.navbar .button-log:focus {
  outline: none;
}

.form-log {
  min-width: 350px;
  width: 70%;
  /* background-color: gainsboro; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 650px;
  /* for test */
  margin-bottom: 200px;
  box-shadow: 5px 5px 10px gray;
}

.form-log .zenix-intro {
  /* background-color: blue; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}
.form-log .zenix-intro .white-logo {
  width: 40px;
  height: 40px;
  border-radius: 30%;
  margin-right: 20px;
  background-color: blue;
}

.input-log {
  margin-bottom: 20px;
  width: 300px;
  height: 35px;
  font-size: 20px;
  padding-left: 20px;
  border-radius: 3px;
  border: 1px solid rgb(235, 232, 232);
  /* box-shadow: 1px 0px 4px gray; */
}

input:focus {
  outline: none;
}
.input-log:focus {
  background-color: rgb(240, 238, 238);
}
button:focus {
  outline: none;
}
.form-log .button-log {
  margin-top: 10px;
  border: none;
  font-size: 20px;
  color: white;
  background: blue;
  width: 300px;
  height: 35px;
  margin-bottom: 20px;
  border-radius: 5px;
}

.form-log .button-log:hover {
  background: darkblue;
}

.form-search {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70%;
  /* background-color: red; */
  border-radius: 20px;
  padding-left: 1px;
  padding-right: 5px;
  background: gainsboro;
  margin-left: 5px;
}

.search-icon {
  margin-left: 4px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-top: 6px;
  /* position: absolute; */
}
.input-search {
  width: 200px;
  height: 30px;
  border: none;
  background: gainsboro;
  /* width: 10px; */
  font-size: 18px;
}

.none {
  display: none;
}
.white {
  background: white;
}

.extend-icon {
  width: 35px;
  height: 35px;
  margin-right: 20px;
}

.navbar-mobile {
  width: 600px;
  height: 1000px;
  margin-top: 60px;
  background-color: white;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  /* align-items: center; */
  top: 1px;
  position: fixed;
  z-index: 5;
  /* opacity: 0.95; */
}
.navbar-mobile .item {
  /* margin: auto; */
  background-color: white;
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
  text-align: center;
  margin-bottom: 5px;
  box-shadow: 0px 3px 5px gray;
}
.navbar-mobile .navbar-introduce {
  background-color: green;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
  margin-top: 2px;
}

.navbar-mobile .navbar-introduce .avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
